import {
  mapActions,
  mapState
} from 'vuex';
export default {
  computed: {
    ...mapState('datashare', [
      'storeApiMsg'
    ])
  },
  methods: {
    ...mapActions('datashare', [
      'saveApiMsg'
    ])
  }
};



