<template>
  <div class="test-api">
    <div class="content">
      <p class="title">
        <span>API地址</span>
        <span class="path">{{ storeApiMsg.path }}</span>
      </p>
      <div class="req-msg">
        <h5 class="title">请求信息</h5>
        <div class="request">
          <div
            v-for="(item, index) in testData"
            :key="index"
            class="fields"
          >
            <gs-checkbox
              v-model="item.check"
              class="name"
            >
              <span
                :title="item.columnName"
              >{{ item.columnName }}</span>
            </gs-checkbox>
            <span>=</span>
            <span class="value">
              <gs-date-picker
                v-if="item.type === 'timestamp'"
                v-model="item.value"
                type="date-time"
              />
              <gs-radio-group
                v-else-if="item.type === 'boolean'"
                v-model="item.value"
              >
                <gs-radio :label="true" />
                <gs-radio :label="false" />
              </gs-radio-group>
              <gs-input-number
                v-else-if="numberType.includes(item.type)"
                v-model="item.value"
              />
              <gs-input
                v-else
                v-model="item.value"
              />
            </span>
          </div>
          <div class="fields">
            <span class="name">limit</span>
            <span>=</span>
            <span class="value">
              <gs-input-number
                :precision="0"
                v-model="limit"
                :min="0"
                :max="100"
              />
            </span>
          </div>
          <div class="fields">
            <span class="name">offset</span>
            <span>=</span>
            <span class="value">
              <gs-input-number
                :precision="0"
                v-model="offset"
                :min="0"
              />
            </span>
          </div>
        </div>
        <div class="test-operation">
          <gs-button
            :disabled="!btnLoading"
            @click="stopTest"
          >停止测试</gs-button>
          <gs-button
            :loading="btnLoading"
            type="primary"
            @click="startTest"
          >开始测试</gs-button>
        </div>
      </div>
      <div class="rep-res">
        <div class="response">
          <gs-tabs
            v-model="activeName"
            type="content-tab"
          >
            <gs-tab-pane
              label="编码模式"
              name="json"
            >
              <gs-textarea
                :value="testResStringify"
                :autosize="{
                  minRows: 8,
                  maxRows: 12
                }"
                readonly
              />
            </gs-tab-pane>
            <gs-tab-pane
              label="表格模式"
              name="table"
            >
              <el-table
                :data="testRes"
              >
                <el-table-column
                  v-for="(item, index) in tableHeader"
                  :key="index"
                  :label="item"
                  :prop="item"
                />
              </el-table>
            </gs-tab-pane>
          </gs-tabs>
        </div>
      </div>
    </div>
    <div class="operation">
      <gs-button
        @click="handleBack"
      >上一步</gs-button>
      <gs-button
        type="primary"
        @click="handleNext"
      >完成</gs-button>
    </div>
  </div>
</template>
<script>
import mixins from '../mixin.js';
import * as service from 'src/service/data-share/index';
import axios from 'axios';
let randomName = Math.random().toString(36).slice(-8);
export default {
  mixins: [mixins],
  data() {
    return {
      testData: [],
      limit: 10,
      offset: 0,
      btnLoading: false,
      activeName: 'json',
      testRes: [],
      numberType: ['double', 'float', 'int', 'bigint', 'decimal']
    };
  },
  computed: {
    tableHeader() {
      const {testRes} = this;
      if (testRes && testRes[0]) {
        return Object.keys(testRes[0]);
      } else {
        return [];
      }
    },
    testResStringify() {
      return JSON.stringify(this.testRes, null, 4);
    }
  },
  activated() {
    this.testData = (this.storeApiMsg.fields || [])
      .filter(item => item.filter)
      .map(item => {
        const column = (this.storeApiMsg.column || []).find(i => i.columnName === item.columnName);
        return {columnName: item.columnName, value: item.defaultValue || '', check: true, type: column.type, fill: item.fill};
      });
  },
  methods: {
    handleNext() {
      this.$emit('next');
    },
    handleBack() {
      this.$emit('back');
    },
    stopTest() {
      this.source.cancel();
      service.stopTestApi(randomName).then(data => {
        this.$Message.success(`停止成功`);
      });
    },
    startTest() {
      if (this.checkTestInput()) {
        const {
          tableGuid,
          dbGuid,
          maxRowNumber
        } = this.storeApiMsg;
        const parameter = this.testData.filter(item => item.check)
          .map(item => {
            if (item.type === 'timestamp' && item.value) {
              return {columnName: item.columnName, value: [item.value.unix()]};
            }
            return {columnName: item.columnName, value: [item.value]};
          });
        const queryFields = this.storeApiMsg.column.map(item => item.columnName);
        const params = {
          tableGuid,
          dbGuid,
          maxRowNumber,
          parameter,
          queryFields,
          limit: this.limit,
          offset: this.offset
        };
        randomName = Math.random().toString(36).slice(-8);
        this.btnLoading = true;
        const { CancelToken } = axios;
        this.source = CancelToken.source();
        service.testApi({params, randomName}, this.source.token).then(data => {
          this.testRes = data;
          this.btnLoading = false;
        }).catch(e => {
          this.btnLoading = false;
          console.log('Request canceled', e.message);
        });
      }
    },
    checkTestInput() {
      const fill = this.testData.filter(item => item.fill && !item.check);
      if (fill && fill.length) {
        const str = fill.map(i => i.columnName).join(',');
        this.$Message.error(`${str}字段为必填字段，请选中并输入数据`);
        return false;
      }
      const checkStr = this.testData.filter(item => item.check)
        .filter(item => {
          if (
            (this.numberType.includes(item.type) && item.value === '') ||
          (item.type === 'timestamp' && item.value === '')
          ) {
            return true;
          } else {
            return false;
          }
        })
        .map(i => i.columnName).join(',');
      if (checkStr) {
        this.$Message.error(`${checkStr}字段为必填字段，请输入数据`);
        return false;
      }
      return true;
    }
  }
};
</script>



